* {
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
    font-family: "Roboto", sans-serif;
    scroll-behavior: smooth;
}

.AuthWrapper {
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
}

.TextWrapper {
    margin-bottom: 5rem;
    width: 40%;
    line-height: 1.5;
}

.TextWrapper h1 {
    line-height: 1;
    font-size: 2.5rem;
}

.TextWrapper p {
    margin-top: 3%;
    width: 80%;
}

.Auth {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.AuthPanel {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.authForm {
    width: 30vw;
    height: 70vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.authForm>input,
.page>input {
    height: 2.9rem;
    width: 100%;
    border: 1px solid hsla(0, 0%, 0%, 10%);
    border-radius: 7px;
    padding: 0 3%;
    margin: 2% 0;
    background-color: #fafafa;
}

.authForm>input:focus,
.page>input:focus {
    outline: none;
}

.inputlabel {
    align-self: flex-start;
}

.authForm>input[type="submit"] {
    background-color: #f5d042;
    border: none;
    font-size: 1.3rem;
    font-weight: 500;
    height: 3rem;
    width: 90%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.option {
    display: flex;
    width: 90%;
    margin: 3% 0;
    justify-content: space-between;
}

.option>div {
    display: flex;
    align-items: center;
    gap: 3px;
}

.option {
    font-size: clamp(0.5vw, 20px, 1rem);
}

.pages {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.page {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    right: -100%;
}

.page1 {
    right: 0%;
}

.authForm>input[type="submit"]:hover {
    box-shadow: 0px 0px 20px hsl(48, 78%, 73%, 0.8);
}

.switcher {
    height: 12vh;
}

.switcher ul {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
}

.switer_li {
    height: 60%;
    width: 11rem;
    display: grid;
    place-content: center;
    border-radius: 20px;
    cursor: pointer;
    z-index: 1;
}

.switer_li.active {
    background-color: #f5d042;
}

@media screen and (max-width: 700px) {
    .AuthWrapper {
        flex-direction: column-reverse;
    }
    .authForm {
        width: 80vw;
    }
    .TextWrapper {
        width: 100%;
        margin-top: 6vh;
        line-height: 1.5;
    }
    .TextWrapper h1 {
        line-height: 1;
        font-size: 2rem;
    }
    .TextWrapper p {
        margin-top: 8%;
        width: 100%;
    }
}